.recent-transactions {
  width: 100%;
}

.transaction-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-height: 400px;
  overflow-y: auto;
  padding-right: 8px;
}

.transaction-list::-webkit-scrollbar {
  width: 6px;
}

.transaction-list::-webkit-scrollbar-track {
  background: transparent;
}

.transaction-list::-webkit-scrollbar-thumb {
  background: rgba(139, 147, 168, 0.3);
  border-radius: 3px;
}

.transaction-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px;
  background: rgba(26, 31, 46, 0.4);
  border-radius: 12px;
  border: 1px solid rgba(139, 147, 168, 0.06);
  transition: all 0.2s ease;
  cursor: pointer;
}

.transaction-item:hover {
  background: rgba(26, 31, 46, 0.6);
  border-color: rgba(0, 217, 255, 0.2);
  transform: translateX(4px);
}

.transaction-icon {
  width: 44px;
  height: 44px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  flex-shrink: 0;
}

.transaction-icon.expense {
  background: rgba(249, 115, 22, 0.15);
}

.transaction-icon.income {
  background: rgba(16, 185, 129, 0.15);
}

.transaction-details {
  flex: 1;
  min-width: 0;
}

.transaction-name {
  font-size: 14px;
  font-weight: 600;
  color: #FFFFFF;
  margin-bottom: 4px;
  word-break: break-word;
}

.transaction-category {
  font-size: 12px;
  color: #8B93A8;
  text-transform: capitalize;
}

.transaction-meta {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 4px;
  flex-shrink: 0;
}

.transaction-date {
  font-size: 12px;
  color: #8B93A8;
}

.transaction-amount {
  font-size: 14px;
  font-weight: 700;
  text-align: right;
}

.transaction-amount.income {
  color: #10B981;
}

.transaction-amount.expense {
  color: #F87171;
}

/* Responsive Design */
@media (max-width: 768px) {
  .transaction-item {
    padding: 14px;
    gap: 10px;
  }

  .transaction-icon {
    width: 40px;
    height: 40px;
    font-size: 18px;
  }

  .transaction-name {
    font-size: 13px;
  }

  .transaction-category {
    font-size: 11px;
  }

  .transaction-date {
    font-size: 11px;
  }

  .transaction-amount {
    font-size: 13px;
  }
}

@media (max-width: 640px) {
  .transaction-item {
    padding: 12px;
    gap: 8px;
  }

  .transaction-icon {
    width: 36px;
    height: 36px;
    font-size: 16px;
  }

  .transaction-name {
    font-size: 12px;
  }

  .transaction-category {
    font-size: 10px;
  }

  .transaction-date {
    font-size: 10px;
  }

  .transaction-amount {
    font-size: 12px;
  }

  .transaction-list {
    max-height: 350px;
  }
}

@media (max-width: 480px) {
  .transaction-item {
    padding: 10px;
    border-radius: 10px;
  }

  .transaction-icon {
    width: 32px;
    height: 32px;
    font-size: 14px;
  }

  .transaction-name {
    font-size: 11px;
  }

  .transaction-category {
    font-size: 9px;
  }

  .transaction-date {
    font-size: 9px;
  }

  .transaction-amount {
    font-size: 11px;
  }

  .transaction-list {
    gap: 8px;
    max-height: 300px;
  }
}

