.login-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #0F1419 0%, #1A1F2E 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.login-card {
  background: rgba(35, 43, 60, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(139, 147, 168, 0.2);
  border-radius: 24px;
  padding: 40px;
  width: 100%;
  max-width: 400px;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
}

.login-header {
  text-align: center;
  margin-bottom: 32px;
}

.login-logo {
  width: 60px;
  height: auto;
  margin-bottom: 16px;
}

.login-header h1 {
  font-size: 28px;
  font-weight: 700;
  color: #FFFFFF;
  margin-bottom: 8px;
}

.login-header p {
  color: #8B93A8;
  font-size: 14px;
}

.login-form {
  margin-bottom: 24px;
}

.form-group {
  margin-bottom: 20px;
}

.form-label {
  display: block;
  font-size: 14px;
  font-weight: 600;
  color: #FFFFFF;
  margin-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.form-input {
  width: 100%;
  padding: 14px 16px;
  background: rgba(26, 31, 46, 0.6);
  border: 1px solid rgba(139, 147, 168, 0.2);
  border-radius: 12px;
  color: #FFFFFF;
  font-size: 14px;
  transition: all 0.2s ease;
  box-sizing: border-box;
}

.form-input:focus {
  outline: none;
  border-color: #00D9FF;
  background: rgba(26, 31, 46, 0.8);
  box-shadow: 0 0 0 3px rgba(0, 217, 255, 0.1);
}

.form-input::placeholder {
  color: #8B93A8;
}

.login-btn {
  width: 100%;
  padding: 16px;
  background: linear-gradient(135deg, #00D9FF, #0099CC);
  border: none;
  border-radius: 12px;
  color: #0F1419;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  margin-top: 8px;
}

.login-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0, 217, 255, 0.3);
}

.login-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.error-message {
  background: rgba(248, 113, 113, 0.1);
  border: 1px solid #F87171;
  border-radius: 8px;
  padding: 12px;
  color: #F87171;
  font-size: 14px;
  text-align: center;
  margin-bottom: 16px;
}

.login-footer {
  text-align: center;
  border-top: 1px solid rgba(139, 147, 168, 0.1);
  padding-top: 24px;
}

.login-footer p {
  color: #8B93A8;
  font-size: 14px;
  margin: 0;
}

.link-btn {
  background: none;
  border: none;
  color: #00D9FF;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  text-decoration: underline;
  margin-left: 4px;
  transition: color 0.2s ease;
}

.link-btn:hover {
  color: #FFFFFF;
}

/* Responsive Design */
@media (max-width: 480px) {
  .login-container {
    padding: 16px;
  }

  .login-card {
    padding: 24px;
    border-radius: 16px;
  }

  .login-header h1 {
    font-size: 24px;
  }

  .login-logo {
    width: 48px;
  }
}
