.categories-section {
  width: 100%;
}

.categories-list {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.category-card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 18px;
  background: rgba(26, 31, 46, 0.6);
  border-radius: 14px;
  border: 1px solid rgba(139, 147, 168, 0.08);
  transition: all 0.2s ease;
  cursor: pointer;
}

.category-card:hover {
  background: rgba(26, 31, 46, 0.8);
  border-color: rgba(0, 217, 255, 0.2);
  transform: translateY(-2px);
}

.category-left {
  display: flex;
  align-items: center;
  gap: 14px;
  flex: 1;
}

.category-icon {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  flex-shrink: 0;
  transition: all 0.2s ease;
}

.category-card:hover .category-icon {
  transform: scale(1.05);
}

.category-info {
  flex: 1;
  min-width: 0; /* Allow text to wrap */
}

.category-name {
  font-size: 14px;
  font-weight: 600;
  color: #FFFFFF;
  margin-bottom: 6px;
}

.category-progress {
  width: 100%;
  height: 6px;
  background: rgba(139, 147, 168, 0.2);
  border-radius: 3px;
  overflow: hidden;
}

.category-progress-bar {
  height: 100%;
  border-radius: 3px;
  transition: width 0.3s ease;
}

.category-amount {
  font-size: 14px;
  font-weight: 700;
  color: #FFFFFF;
  text-align: right;
  min-width: 80px;
  flex-shrink: 0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .category-card {
    padding: 16px;
  }

  .category-left {
    gap: 12px;
  }

  .category-icon {
    width: 44px;
    height: 44px;
    font-size: 22px;
  }

  .category-name {
    font-size: 13px;
  }

  .category-amount {
    font-size: 13px;
    min-width: 70px;
  }
}

@media (max-width: 640px) {
  .category-card {
    padding: 14px;
    border-radius: 12px;
  }

  .category-left {
    gap: 10px;
  }

  .category-icon {
    width: 40px;
    height: 40px;
    font-size: 20px;
  }

  .category-name {
    font-size: 12px;
    margin-bottom: 4px;
  }

  .category-progress {
    height: 5px;
  }

  .category-amount {
    font-size: 12px;
    min-width: 60px;
  }
}

@media (max-width: 480px) {
  .category-card {
    padding: 12px;
  }

  .category-left {
    gap: 8px;
  }

  .category-icon {
    width: 36px;
    height: 36px;
    font-size: 18px;
  }

  .category-name {
    font-size: 11px;
  }

  .category-amount {
    font-size: 11px;
    min-width: 50px;
  }
}


