.spending-chart-section {
  margin-bottom: 32px;
}

.section-title {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.section-title span {
  color: #FFFFFF;
}

/* Period Selector */
.period-selector {
  display: flex;
  gap: 12px;
  background: rgba(26, 31, 46, 0.5);
  padding: 8px;
  border-radius: 12px;
  width: fit-content;
}

.period-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 10px;
  background: transparent;
  color: #8B93A8;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 14px;
}

.period-btn.active {
  background: #00D9FF;
  color: #0F1419;
  font-weight: 600;
}

.period-btn:hover:not(.active) {
  color: #FFFFFF;
}

/* Chart Card */
.chart-card {
  background: rgba(26, 31, 46, 0.6);
  border-radius: 16px;
  padding: 28px;
  margin-bottom: 32px;
  border: 1px solid rgba(139, 147, 168, 0.08);
  position: relative;
  overflow: hidden;
  width: 100%;
  box-sizing: border-box;
}

.chart-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(0, 217, 255, 0.05) 0%, transparent 100%);
  pointer-events: none;
}

.chart-container {
  position: relative;
  height: 320px;
}

.chart-value {
  position: absolute;
  top: 20px;
  right: 20px;
  background: rgba(0, 217, 255, 0.1);
  border: 1px solid #00D9FF;
  color: #00D9FF;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  backdrop-filter: blur(10px);
  z-index: 10;
  max-width: calc(100% - 40px);
  box-sizing: border-box;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .chart-card {
    padding: 24px;
  }

  .chart-container {
    height: 300px;
  }
}

@media (max-width: 1024px) {
  .section-title {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
  }

  .period-selector {
    align-self: flex-end;
    width: auto;
  }

  .chart-card {
    padding: 20px;
  }

  .chart-container {
    height: 280px;
  }
}

@media (max-width: 768px) {
  .section-title {
    font-size: 20px;
    gap: 12px;
  }

  .period-selector {
    padding: 6px;
    gap: 8px;
  }

  .period-btn {
    padding: 8px 14px;
    font-size: 13px;
  }

  .chart-card {
    padding: 16px;
    margin-bottom: 24px;
  }

  .chart-container {
    height: 250px;
  }

  .chart-value {
    font-size: 12px;
    padding: 6px 10px;
  }
}

@media (max-width: 640px) {
  .section-title {
    font-size: 18px;
    margin-bottom: 16px;
  }

  .period-selector {
    width: 100%;
    justify-content: center;
    padding: 4px;
  }

  .period-btn {
    padding: 6px 12px;
    font-size: 12px;
    flex: 1;
    min-width: 0;
  }

  .chart-card {
    padding: 12px;
    border-radius: 12px;
  }

  .chart-container {
    height: 220px;
  }

  .chart-value {
    font-size: 11px;
    padding: 4px 8px;
    top: 12px;
    right: 12px;
  }
}

@media (max-width: 480px) {
  .section-title {
    font-size: 16px;
  }

  .chart-card {
    padding: 8px;
  }

  .chart-container {
    height: 200px;
  }

  .chart-value {
    font-size: 10px;
    padding: 3px 6px;
  }

  .period-btn {
    padding: 5px 10px;
    font-size: 11px;
  }
}

/* Simple Chart Styles */
.simple-chart {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.chart-bars {
  display: flex;
  align-items: end;
  justify-content: space-between;
  height: 250px;
  padding: 20px 10px 40px;
  gap: 4px;
}

.chart-bar-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
  min-width: 0;
}

.chart-bar {
  width: 100%;
  max-width: 40px;
  border-radius: 4px 4px 0 0;
  transition: all 0.3s ease;
  position: relative;
}

.chart-bar.highlighted {
  box-shadow: 0 0 10px rgba(0, 217, 255, 0.5);
}

.chart-bar:hover {
  opacity: 0.8;
}

.chart-label {
  margin-top: 8px;
  font-size: 11px;
  color: #8B93A8;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

/* Chart Error State */
.chart-error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 320px;
  color: #8B93A8;
  font-size: 14px;
}

.chart-error button {
  margin-top: 12px;
  padding: 8px 16px;
  background: rgba(0, 217, 255, 0.1);
  border: 1px solid #00D9FF;
  border-radius: 6px;
  color: #00D9FF;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.2s ease;
}

.chart-error button:hover {
  background: rgba(0, 217, 255, 0.2);
}

@media (max-width: 768px) {
  .chart-bars {
    height: 200px;
    padding: 15px 5px 35px;
  }

  .chart-bar {
    max-width: 30px;
  }

  .chart-label {
    font-size: 10px;
  }
}

@media (max-width: 640px) {
  .chart-bars {
    height: 180px;
    padding: 10px 3px 30px;
    gap: 2px;
  }

  .chart-bar {
    max-width: 25px;
  }

  .chart-label {
    font-size: 9px;
    margin-top: 6px;
  }
}

@media (max-width: 480px) {
  .chart-bars {
    height: 160px;
    padding: 8px 2px 25px;
  }

  .chart-bar {
    max-width: 20px;
  }

  .chart-label {
    font-size: 8px;
    margin-top: 4px;
  }
}
