/* Modal Overlay */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(15, 20, 25, 0.8);
  backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
}

.modal-overlay.ReactModal__Overlay--after-open {
  opacity: 1;
  transition: opacity 0.2s ease;
}

.modal-overlay.ReactModal__Overlay--before-close {
  opacity: 0;
}

/* Modal Content */
.modal-content {
  background: rgba(35, 43, 60, 0.95);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(139, 147, 168, 0.2);
  border-radius: 24px;
  width: 100%;
  max-width: 500px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
}

.modal-content.ReactModal__Content--after-open {
  transform: scale(1);
  transition: transform 0.2s ease;
}

.modal-content.ReactModal__Content--before-close {
  transform: scale(0.95);
}

/* Modal Header */
.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 24px 32px;
  border-bottom: 1px solid rgba(139, 147, 168, 0.1);
}

.modal-title {
  font-size: 24px;
  font-weight: 700;
  color: #FFFFFF;
  margin: 0;
}

.modal-close {
  background: none;
  border: none;
  font-size: 28px;
  color: #8B93A8;
  cursor: pointer;
  padding: 4px 8px;
  border-radius: 8px;
  transition: all 0.2s ease;
  line-height: 1;
}

.modal-close:hover {
  background: rgba(139, 147, 168, 0.1);
  color: #FFFFFF;
}

/* Modal Body */
.modal-body {
  padding: 32px;
}

/* Form Styles */
.form-group {
  margin-bottom: 24px;
}

.form-label {
  display: block;
  font-size: 14px;
  font-weight: 600;
  color: #FFFFFF;
  margin-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.form-input {
  width: 100%;
  padding: 14px 16px;
  background: rgba(26, 31, 46, 0.6);
  border: 1px solid rgba(139, 147, 168, 0.2);
  border-radius: 12px;
  color: #FFFFFF;
  font-size: 14px;
  transition: all 0.2s ease;
  box-sizing: border-box;
}

.form-input:focus {
  outline: none;
  border-color: #00D9FF;
  background: rgba(26, 31, 46, 0.8);
  box-shadow: 0 0 0 3px rgba(0, 217, 255, 0.1);
}

.form-input::placeholder {
  color: #8B93A8;
}

/* Transaction Type Tabs */
.transaction-type-tabs {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
  background: rgba(26, 31, 46, 0.5);
  padding: 8px;
  border-radius: 12px;
}

.type-tab {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 12px 16px;
  background: transparent;
  border: 1px solid rgba(139, 147, 168, 0.2);
  border-radius: 10px;
  color: #8B93A8;
  font-weight: 500;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.type-tab:hover {
  border-color: rgba(139, 147, 168, 0.3);
  background: rgba(15, 20, 25, 0.7);
}

.type-tab.active {
  border-color: #00D9FF;
  background: rgba(0, 217, 255, 0.1);
  color: #00D9FF;
}

.type-tab span {
  font-size: 16px;
}

/* Category Grid */
.category-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;
}

.category-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  padding: 16px 12px;
  background: rgba(26, 31, 46, 0.6);
  border: 1px solid rgba(139, 147, 168, 0.2);
  border-radius: 12px;
  color: #FFFFFF;
  font-weight: 500;
  font-size: 13px;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: center;
}

.category-btn:hover {
  border-color: rgba(139, 147, 168, 0.3);
  background: rgba(15, 20, 25, 0.7);
}

.category-btn.active {
  border-color: #00D9FF;
  background: rgba(0, 217, 255, 0.1);
  color: #00D9FF;
}

.category-icon-btn {
  font-size: 24px;
}

/* Amount Input Group */
.amount-input-group {
  position: relative;
}

.currency-symbol {
  position: absolute;
  left: 16px;
  top: 50%;
  transform: translateY(-50%);
  color: #8B93A8;
  font-weight: 600;
  pointer-events: none;
  z-index: 1;
}

.amount-input {
  padding-left: 36px;
  font-size: 20px;
  font-weight: 600;
}

/* Form Footer */
.form-footer {
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
  margin-top: 32px;
  padding-top: 24px;
  border-top: 1px solid rgba(139, 147, 168, 0.1);
}

.btn-modal {
  padding: 14px 24px;
  border: none;
  border-radius: 12px;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-modal-cancel {
  background: rgba(139, 147, 168, 0.1);
  color: #FFFFFF;
  border: 1px solid rgba(139, 147, 168, 0.2);
}

.btn-modal-cancel:hover {
  background: rgba(139, 147, 168, 0.15);
  border-color: rgba(139, 147, 168, 0.3);
}

.btn-modal-submit {
  background: linear-gradient(135deg, #00D9FF, #0099CC);
  color: #0F1419;
}

.btn-modal-submit:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0, 217, 255, 0.3);
}

.btn-modal-submit:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

/* Responsive Design */
@media (max-width: 640px) {
  .modal-content {
    width: 95%;
    border-radius: 20px;
  }

  .modal-header {
    padding: 20px 24px;
  }

  .modal-body {
    padding: 24px;
  }

  .category-grid {
    grid-template-columns: repeat(4, 1fr);
    gap: 8px;
  }

  .category-btn {
    padding: 12px 8px;
    font-size: 12px;
  }

  .category-icon-btn {
    font-size: 20px;
  }

  .transaction-type-tabs {
    gap: 8px;
    padding: 6px;
  }

  .type-tab {
    padding: 10px 12px;
    font-size: 13px;
  }

  .form-footer {
    gap: 8px;
  }

  .btn-modal {
    padding: 12px 20px;
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .modal-overlay {
    padding: 10px;
  }

  .modal-header {
    padding: 16px 20px;
  }

  .modal-body {
    padding: 20px;
  }

  .category-grid {
    grid-template-columns: repeat(3, 1fr);
  }

  .category-btn {
    padding: 10px 6px;
    font-size: 11px;
  }

  .category-icon-btn {
    font-size: 18px;
  }
}
