.home {
  min-height: calc(100vh - 72px);
  padding: 40px 0;
  overflow-x: hidden;
  width: 100%;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 32px;
  width: 100%;
  box-sizing: border-box;
}

.page-header {
  margin-bottom: 24px;
}

.page-header h1 {
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 8px;
  color: #ffffff;
}

.page-header p {
  color: #8B93A8;
  font-size: 15px;
}

/* Layout Grid */
.layout {
  display: grid;
  grid-template-columns: 1fr 0.8fr;
  gap: 24px;
  margin-top: 24px;
}

/* Section */
.section {
  background: rgba(35, 43, 60, 0.4);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(139, 147, 168, 0.1);
  border-radius: 20px;
  padding: 24px;
  transition: all 0.3s ease;
  width: 100%;
  box-sizing: border-box;
}

.section:hover {
  border-color: rgba(0, 217, 255, 0.2);
  background: rgba(35, 43, 60, 0.6);
}

/* Transactions Section */
.transactions-section {
  margin-top: 32px;
}

.section-title {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.section-title {
  color: #FFFFFF;
}

.reset-button {
  padding: 6px 12px;
  background: rgba(139, 147, 168, 0.1);
  border: 1px solid rgba(139, 147, 168, 0.2);
  border-radius: 8px;
  color: #8B93A8;
  font-size: 12px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.reset-button:hover {
  background: rgba(139, 147, 168, 0.15);
  border-color: rgba(139, 147, 168, 0.3);
  color: #FFFFFF;
}

/* Left Column */
.left-column {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 0; /* Allow flex shrinking */
}

/* Right Column */
.right-column {
  display: flex;
  flex-direction: column;
  gap: 32px;
  width: 100%;
  min-width: 0; /* Allow flex shrinking */
}

/* Summary Card */
.summary-card {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.summary-item {
  background: rgba(26, 31, 46, 0.6);
  padding: 24px;
  border-radius: 16px;
  border: 1px solid rgba(139, 147, 168, 0.08);
}

.summary-label {
  font-size: 13px;
  color: #8B93A8;
  font-weight: 500;
  margin-bottom: 12px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.summary-value {
  font-size: 28px;
  font-weight: 700;
  display: flex;
  align-items: baseline;
  gap: 4px;
}

.summary-value.income {
  color: #10B981;
}

.summary-value.expense {
  color: #F87171;
}

.summary-value.balance {
  color: #00D9FF;
}

/* Coming Soon Cards */
.coming-soon-card {
  text-align: center;
  padding: 40px 20px;
  color: #8B93A8;
}

.coming-soon-icon {
  font-size: 48px;
  margin-bottom: 16px;
  opacity: 0.6;
}

.coming-soon-card h3 {
  font-size: 18px;
  font-weight: 600;
  color: #FFFFFF;
  margin-bottom: 8px;
}

.coming-soon-card p {
  font-size: 14px;
}

/* Action Buttons */
.action-buttons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
  margin-top: 32px;
  width: 100%;
}

.btn {
  padding: 14px 24px;
  border: none;
  border-radius: 12px;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.btn-primary {
  background: linear-gradient(135deg, #00D9FF, #0099CC);
  color: #0F1419;
  grid-column: 1 / -1;
  padding: 16px 24px;
  font-size: 15px;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0, 217, 255, 0.3);
}

.btn-secondary {
  background: rgba(139, 147, 168, 0.1);
  color: #FFFFFF;
  border: 1px solid rgba(139, 147, 168, 0.2);
}

.btn-secondary:hover {
  background: rgba(139, 147, 168, 0.15);
  border-color: rgba(139, 147, 168, 0.3);
}

/* Responsive Design */
@media (min-width: 1400px) {
  .layout {
    grid-template-columns: 1.2fr 0.8fr;
    gap: 32px;
  }

  .section {
    padding: 32px;
  }
}

@media (max-width: 1200px) {
  .container {
    padding: 0 24px;
  }

  .layout {
    gap: 24px;
  }

  .section {
    padding: 28px;
  }
}

@media (max-width: 1024px) {
  .layout {
    grid-template-columns: 1fr;
    gap: 24px;
  }

  .container {
    padding: 0 20px;
  }

  .section {
    padding: 24px;
  }

  .summary-card {
    grid-template-columns: 1fr;
    gap: 16px;
  }

  .summary-item {
    padding: 20px;
  }

  .home {
    padding: 32px 0;
  }
}

@media (max-width: 768px) {
  .container {
    padding: 0 16px;
  }

  .section {
    padding: 20px;
    border-radius: 16px;
  }

  .section-title {
    font-size: 20px;
    margin-bottom: 20px;
  }

  .page-header h1 {
    font-size: 28px;
  }

  .page-header {
    margin-bottom: 20px;
  }

  .layout {
    gap: 20px;
  }

  .summary-value {
    font-size: 24px;
  }

  .coming-soon-card {
    padding: 32px 16px;
  }

  .home {
    padding: 24px 0;
  }
}

@media (max-width: 640px) {
  .container {
    padding: 0 12px;
  }

  .section {
    padding: 16px;
    border-radius: 12px;
  }

  .section-title {
    font-size: 18px;
    margin-bottom: 16px;
  }

  .page-header h1 {
    font-size: 24px;
  }

  .page-header p {
    font-size: 14px;
  }

  .summary-item {
    padding: 16px;
  }

  .summary-value {
    font-size: 20px;
  }

  .summary-label {
    font-size: 12px;
    margin-bottom: 8px;
  }

  .action-buttons {
    grid-template-columns: 1fr;
    gap: 8px;
    margin-top: 24px;
  }

  .btn {
    padding: 12px 20px;
    font-size: 13px;
  }

  .btn-primary {
    padding: 14px 20px;
    font-size: 14px;
  }

  .coming-soon-card {
    padding: 24px 12px;
  }

  .coming-soon-icon {
    font-size: 36px;
    margin-bottom: 12px;
  }

  .home {
    padding: 16px 0;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 0 8px;
  }

  .section {
    padding: 12px;
  }

  .page-header {
    margin-bottom: 16px;
  }

  .page-header h1 {
    font-size: 22px;
    margin-bottom: 6px;
  }

  .layout {
    gap: 16px;
  }

  .summary-item {
    padding: 12px;
  }

  .summary-value {
    font-size: 18px;
  }

  .coming-soon-card {
    padding: 20px 8px;
  }
}

/* Reset Modal Styles */
.reset-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(15, 20, 25, 0.8);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
}

.reset-modal-content {
  background: rgba(35, 43, 60, 0.95);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(139, 147, 168, 0.2);
  border-radius: 16px;
  width: 100%;
  max-width: 400px;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
}

.reset-modal-header {
  padding: 20px 24px;
  border-bottom: 1px solid rgba(139, 147, 168, 0.1);
  text-align: center;
}

.reset-modal-title {
  font-size: 18px;
  font-weight: 700;
  color: #FFFFFF;
  margin: 0;
}

.reset-modal-body {
  padding: 24px;
  text-align: center;
}

.reset-modal-body p {
  color: #FFFFFF;
  font-size: 14px;
  margin-bottom: 8px;
}

.reset-modal-warning {
  color: #F87171 !important;
  font-weight: 500;
  font-size: 13px;
  margin-bottom: 0 !important;
}

.reset-modal-footer {
  display: flex;
  gap: 12px;
  padding: 0 24px 24px;
  justify-content: center;
}

.reset-modal-btn {
  flex: 1;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.reset-modal-cancel {
  background: rgba(139, 147, 168, 0.1);
  color: #FFFFFF;
  border: 1px solid rgba(139, 147, 168, 0.2);
}

.reset-modal-cancel:hover {
  background: rgba(139, 147, 168, 0.15);
  border-color: rgba(139, 147, 168, 0.3);
}

.reset-modal-confirm {
  background: linear-gradient(135deg, #F87171, #EF4444);
  color: #FFFFFF;
}

.reset-modal-confirm:hover {
  background: linear-gradient(135deg, #EF4444, #DC2626);
  transform: translateY(-1px);
  box-shadow: 0 8px 24px rgba(248, 113, 113, 0.3);
}

@media (max-width: 480px) {
  .reset-modal-overlay {
    padding: 16px;
  }

  .reset-modal-content {
    border-radius: 12px;
  }

  .reset-modal-header {
    padding: 16px 20px;
  }

  .reset-modal-title {
    font-size: 16px;
  }

  .reset-modal-body {
    padding: 20px;
  }

  .reset-modal-footer {
    padding: 0 20px 20px;
  }

  .reset-modal-btn {
    padding: 12px 16px;
    font-size: 13px;
  }
}
