.navbar {
  background: rgba(15, 20, 25, 0.95);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(139, 147, 168, 0.1);
  padding: 0 32px;
  height: 72px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.3);
  overflow-x: hidden;
  width: 100%;
  box-sizing: border-box;
}

.nav-container {
  max-width: 1400px;
  margin: 0 auto;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav-brand {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.nav-logo {
  height: 32px;
  width: auto;
  object-fit: contain;
}

.nav-actions {
  display: flex;
  gap: 16px;
  align-items: center;
}

.user-info {
  color: #FFFFFF;
  font-size: 14px;
  font-weight: 500;
  margin-right: 8px;
}

.nav-icon {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  background: rgba(35, 43, 60, 0.5);
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  color: #ffffff;
  font-size: 16px;
}

.nav-icon:hover {
  background: rgba(0, 217, 255, 0.1);
  transform: scale(1.05);
}

.logout-btn {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  background: rgba(248, 113, 113, 0.1);
  border: 1px solid rgba(248, 113, 113, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  color: #F87171;
  font-size: 16px;
}

.logout-btn:hover {
  background: rgba(248, 113, 113, 0.2);
  border-color: #F87171;
  transform: scale(1.05);
}

@media (max-width: 768px) {
  .navbar {
    padding: 0 20px;
    height: 64px;
  }

  .nav-logo {
    height: 28px;
  }

  .nav-actions {
    gap: 16px;
  }

  .nav-icon {
    width: 36px;
    height: 36px;
    font-size: 14px;
  }
}

@media (max-width: 640px) {
  .navbar {
    padding: 0 16px;
    height: 60px;
  }

  .nav-logo {
    height: 26px;
  }

  .nav-actions {
    gap: 12px;
  }

  .nav-icon {
    width: 32px;
    height: 32px;
    font-size: 12px;
  }
}

@media (max-width: 480px) {
  .navbar {
    padding: 0 12px;
    height: 56px;
  }

  .nav-logo {
    height: 24px;
  }

  .nav-actions {
    gap: 8px;
  }

  .nav-icon {
    width: 28px;
    height: 28px;
    font-size: 11px;
  }
}
