.App {
  min-height: 100vh;
  background: linear-gradient(135deg, #0F1419 0%, #1A1F2E 100%);
  overflow-x: hidden;
}

main {
  padding-top: 72px; /* Account for fixed header */
}

.loading-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #0F1419 0%, #1A1F2E 100%);
}

.loading-spinner {
  color: #00D9FF;
  font-size: 18px;
  font-weight: 600;
}

/* Responsive header spacing */
@media (max-width: 768px) {
  main {
    padding-top: 64px;
  }
}

@media (max-width: 640px) {
  main {
    padding-top: 60px;
  }
}

@media (max-width: 480px) {
  main {
    padding-top: 56px;
  }
}
